import React from 'react';
import { Row, Col, Card, Form, Button, Table } from 'react-bootstrap';
import Aux from "../../hoc/_Aux";
import DatePicker from 'react-datepicker';
import PropTypes from 'prop-types'
import { connect } from 'react-redux';

import Select from 'react-select';
import AsyncSelect from 'react-select/async';

import ErrorBody from '../../App/components/Reusables/ErrorBody';
import Loader from '../../App/components/Reusables/Loader';
import Jhilikka from '../../App/components/Reusables/Jhilikka';
import { validatePurchaseOrderCreation, validatePurchaseItemsCreation } from '../../validation/purchase';
import { getPurchaseOrderStatus } from '../../api/misc';
import { getItems, getItem } from '../../api/inventory/itemApi';
import { getVendors } from '../../api/inventory/vendorApi';
import isEmpty from '../../validation/isEmpty';
import { createPurchaseOrder } from '../../api/inventory/purchaseOrder';
import { createPurchaseItem } from '../../api/inventory/purchaseItem';
import Num from '../../App/components/Reusables/Num';
import CustomSelectMenu from '../../App/components/Reusables/CustomSelectMenu';
import CreateItemModal from '../Items/CreateItemModal';
import CreateVendorModal from '../Vendors/CreateVendorModal';
import { RESET_FETCH_MORE_OPTIONS, SET_FETCH_MORE_OPTIONS, SET_INSTANCE_CREATION_MODAL, SET_IS_MENU_OPEN } from '../../redux/actions/types';
import store from '../../redux/store';
import CustomSelectMenuList from '../../App/components/Reusables/CustomSelectMenuList';
import DEMO from '../../globals/constant';
import useMousetrap from 'react-hook-mousetrap';



const purchaseItemModel = {
    item_id: '',
    quantity: 1,
    defective: 0,
    weight: 0,
    price: 0,
    discount: 0,
    discountType: 'percent',
    total: '',
    itemStatus: ''
}

const Create = (props) => {

    const [loading, setLoading] = React.useState(true)
    const [submitting, setSubmitting] = React.useState(false);
    const [barcodeDisabled, setBarcodeDisabled] = React.useState(false)
    const [orderStatuses, setOrderStatuses] = React.useState([])

    const [vendorInputText, setVendorInputText] = React.useState('');
    const [defaultVendorOptions, setDefaultVendorOptions] = React.useState([])
    const [vendorOptionsMaxLength, setVendorOptionsMaxLength] = React.useState(0)

    const [defaultItemOptions, setDefaultItemOptions] = React.useState([]);
    const [itemOptionsMaxLength, setItemOptionsMaxLength] = React.useState(0);
    const [itemSearchMode, setItemSearchMode] = React.useState('static');
    const [itemInputText, setItemInputText] = React.useState('');

    const [purchaseOrderData, setPurchaseOrderData] = React.useState({
        inputText: '',
        vendor_id: null, //will be a vendor object
        vendorName: null,
        invoiceNo: '',
        invoicedOn: new Date(),
        completedOn: new Date(),
        orderStatus: '',
        extraDiscount: 0,
        extraDiscountType: 'percent',
        enteredBarcode: '',
    })
    const [purchaseItemsData, setPurchaseItemsData] = React.useState([{ ...purchaseItemModel }]);
    const [createItemModalData, setCreateItemModalData] = React.useState({ showModal: null, activeRow: null });
    const [createVendorModalData, setCreateVendorModalData] = React.useState(false);

    const [orderErrors, setOrderErrors] = React.useState({})
    const [itemErrors, setItemErrors] = React.useState([])

    const barcodeRef = React.useRef(null);

    const getVendors_ = async (signal) => {
        await getVendors({
            "action": 'get',
            "filter": 'none',
            "start": defaultVendorOptions.length,
            "end": defaultVendorOptions.length + DEMO.INSTANCE_PER_LOAD
        }, signal)
            .then(res => {
                console.log('here is one', res);
                if (res.json.status) {
                    setDefaultVendorOptions([
                        ...defaultVendorOptions,
                        ...res.json.vendors.map(instance => ({ label: `${instance.first_name} ${instance.middle_name} ${instance.last_name}`, value: instance.id }))
                    ]);
                    setVendorOptionsMaxLength(res.json.count)
                    store.dispatch({
                        type: SET_FETCH_MORE_OPTIONS,
                        payload: ['fetchMoreOptionsVendors', false]
                    })
                } else {
                    Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' })
                }
            })
            .catch(err => {
                console.log(err)
            })
    }

    const getItems_ = async (signal) => {
        console.log('api call fired to get items')
        await getItems({
            "action": 'get',
            "filter": 'none',
            "start": defaultItemOptions.length,
            "end": defaultItemOptions.length + DEMO.INSTANCE_PER_LOAD
        }, signal)
            .then(res => {
                console.log('here is one', res);
                if (res.json.status) {
                    setDefaultItemOptions([
                        ...defaultItemOptions,
                        ...res.json.items.map((itm, index) => ({ label: `${itm.name}`, value: itm.id, price: itm.average_cost_price, weight: itm.weight || 0 }))
                    ]);
                    setItemOptionsMaxLength(res.json.count)
                    store.dispatch({
                        type: SET_FETCH_MORE_OPTIONS,
                        payload: ['fetchMoreOptionsItems', false]
                    })
                } else {
                    Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' })
                }
            })
            .catch(err => {
                console.log(err)
            })
    }

    //Component did mount 
    React.useEffect(() => {
        const controller = new AbortController();
        getPurchaseOrderStatus(null, controller.signal)
            .then((res) => {
                if (res.json.status) {
                    setOrderStatuses(res.json.data);
                } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }

            }).catch((err) => {
                console.log(err)
            }).finally(() => {
                setLoading(false)
            });
        getVendors_(controller.signal);
        getItems_(controller.signal);
        return () => controller.abort();
    }, []);





    //onchange of the item input text, update the item search mode
    React.useEffect(() => {
        if (itemInputText) {
            setItemSearchMode('dynamic');
        } else {
            setItemSearchMode('static')
        }
    }, [itemInputText])



    const amendOrderErrorOf = (field) => {
        setOrderErrors({ ...orderErrors, [field]: '' })
    }

    const amendItemsErrorOf = (row, field) => {
        let tempItemErrors = itemErrors;
        if (tempItemErrors[row]) {
            tempItemErrors[row][field] = '';
            setItemErrors([...tempItemErrors])
        }
    }

    const loadVendorOptions = async (inputText) => {
        const requestJson = { action: 'get', filter: 'name', first_name: inputText }
        return getVendors(requestJson)
            .then((res) => {
                if (res.json.status) {
                    return res.json.vendors.map((ven, index) => ({ label: `${ven.first_name} ${ven.middle_name} ${ven.last_name}`, value: ven.id }));
                }
            }).catch((err) => {
                console.log(err)
            });
    }

    const loadItemOptions = async (inputText) => {
        const requestJson = { action: 'get', filter: 'name', name: inputText, start: 0, end: 50 }
        return getItems(requestJson)
            .then((res) => {
                console.log(res)
                if (res.json.status) {
                    return res.json.items.map((itm, index) => ({ label: `${itm.name}`, value: itm.id, price: itm.average_cost_price, weight: itm.weight || 0 }));
                } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
            }).catch((err) => {
                console.log(err)
            });

    }

    const _vendorInputChange = (inputText) => {
        setVendorInputText(inputText)
    }
    const _scrollVendorMenuToBottom = (e) => {
        e.preventDefault();
        if (vendorOptionsMaxLength > defaultVendorOptions.length && !vendorInputText) {
            store.dispatch({
                type: SET_FETCH_MORE_OPTIONS,
                payload: ['fetchMoreOptionsVendors', true]
            })
            getVendors_();
        }
    }
    const _selectedVendorChange = ven => {
        amendOrderErrorOf('vendor_id')
        try {
            setPurchaseOrderData({ ...purchaseOrderData, vendor_id: ven.value, vendorName: ven.label })
        } catch (e) {
            setPurchaseOrderData({ ...purchaseOrderData, vendor_id: '', vendorName: '' })

        }
    };

    const _invoicedOnChange = (selectedDate) => {
        amendOrderErrorOf('invoicedOn')
        setPurchaseOrderData({ ...purchaseOrderData, invoicedOn: selectedDate })
    }

    const _completedOnChange = (selectedDate) => {
        amendOrderErrorOf('completedOn')
        setPurchaseOrderData({ ...purchaseOrderData, completedOn: selectedDate })
    }



    const _purchaseOrderDataChange = (e) => {
        e.preventDefault();
        const type = e.target.type
        const target = e.target.name
        const value = e.target.value
        amendOrderErrorOf(target)
        setPurchaseOrderData({ ...purchaseOrderData, [target]: type === 'number' ? parseFloat(value) || '' : value })

    }
    const _keydownBarcode = async (e) => {
        console.log(e.key)
        if (e.key === 'Enter') {
            setBarcodeDisabled(true)
            //get the item by id
            try {
                const getItemRes = await getItems({ action: 'get', 'filter': 'barcode', 'barcode': e.target.value });
                console.log(getItemRes, 'is the item details');
                //if status true response
                if (getItemRes.json.status) {

                    //if status true response but empty array response
                    if (getItemRes.json.items.length < 1) {
                        await setPurchaseOrderData({ ...purchaseOrderData, enteredBarcode: '' })
                        await setBarcodeDisabled(false)
                        barcodeRef.current.focus()
                        Jhilikka.fire({ icon: 'error', title: "No item found with given barcode!", background: '#ffe5de' })
                    }
                    const receivedItem = getItemRes.json.items[0]
                    let tempPurchaseItemsData = [...purchaseItemsData];
                    let foundItem = purchaseItemsData.find(item => item.item_id === getItemRes.json.items[0].id);
                    console.log(foundItem)
                    if (foundItem) {
                        console.log("item is found already");
                        const foundItemIndex = purchaseItemsData.indexOf(foundItem);
                        tempPurchaseItemsData[foundItemIndex] = {
                            ...tempPurchaseItemsData[foundItemIndex],
                            ...foundItem,
                            quantity: tempPurchaseItemsData[foundItemIndex].quantity + 1,
                        };
                        setPurchaseItemsData(tempPurchaseItemsData);

                    } else {
                        let victimRow = 0;
                        const addedItem = {
                            item_id: receivedItem.id,
                            itemName: receivedItem.name,
                            quantity: 1,
                            defective: 0,
                            weight: receivedItem.weight,
                            price: receivedItem.average_cost_price,
                            discount: 0,
                            discountType: 'percent',
                            total: '',
                            itemStatus: 'addedtocirculation'
                        }

                        if (purchaseItemsData.length === 1 && purchaseItemsData[0].item_id === '') {
                            tempPurchaseItemsData[0] = { ...tempPurchaseItemsData[0], ...addedItem };
                        } else {
                            victimRow = purchaseItemsData.length;
                            tempPurchaseItemsData = [...tempPurchaseItemsData, addedItem]
                        }
                        await setPurchaseItemsData(tempPurchaseItemsData);
                    }
                    await setPurchaseOrderData({ ...purchaseOrderData, enteredBarcode: '' })
                } else {

                    await setPurchaseOrderData({ ...purchaseOrderData, enteredBarcode: '' })
                    await setBarcodeDisabled(false)
                    barcodeRef.current.focus()
                    Jhilikka.fire({ icon: 'error', title: getItemRes.json.error, background: '#ffe5de' })
                }
            } catch (err) {
                console.log(err);
            } finally {
                (async () => {
                    await setBarcodeDisabled(false)
                    barcodeRef.current.focus()
                })()
            }
        }
    }


    const _itemInputChange = (itemInputText) => {
        setItemInputText(itemInputText);
    }
    const _scrollItemMenuToBottom = (e) => {
        e.preventDefault();
        if (itemOptionsMaxLength > defaultItemOptions.length && !itemInputText) {
            store.dispatch({
                type: SET_FETCH_MORE_OPTIONS,
                payload: ['fetchMoreOptionsItems', true]
            })
            getItems_();
        }
    }
    const _selectedItemChange = (row, item) => {
        amendItemsErrorOf(row, 'item_id');
        console.log(item)
        const tempItemsData = [...purchaseItemsData];
        tempItemsData[row] = {
            ...tempItemsData[row],
            item_id: item.value,
            itemName: item.label,
            price: item.price,
            weight: item.weight,
            itemStatus: 'addedtocirculation',
        }
        setPurchaseItemsData(tempItemsData)
    }

    const _purchaseItemsDataChange = (row, e) => {
        const target = e.target.name;
        const tempItemsData = purchaseItemsData;
        amendItemsErrorOf(row, target)
        tempItemsData[row][e.target.name] = e.target.type === 'number' ? parseFloat(e.target.value) || "" : e.target.value;
        setPurchaseItemsData([...tempItemsData]);
    }

    const _addPurchaseItem = (e) => {
        e.preventDefault();
        setPurchaseItemsData([...purchaseItemsData, { ...purchaseItemModel }])
    }

    const _removePurchaseItem = (removedItemIdx) => {
        const tempItemsData = purchaseItemsData;
        tempItemsData.splice(removedItemIdx, 1);
        setPurchaseItemsData([...tempItemsData]);

        const tempItemErrors = [...itemErrors];
        tempItemErrors.splice(removedItemIdx, 1);
        setItemErrors(tempItemErrors)
    }

    useMousetrap('alt+a', () => barcodeRef.current.focus())
    useMousetrap('alt+s', () => _submit())

    const _submit = () => {
        // e.preventDefault();
        const orderValidity = validatePurchaseOrderCreation(purchaseOrderData);
        const itemsValidity = validatePurchaseItemsCreation(purchaseItemsData)
        if (!orderValidity.isValid) {
            console.log(orderValidity.errors)
            setOrderErrors({ ...orderValidity.errors });
            Jhilikka.fire({ icon: 'warning', title: 'Fix errros and re-submit!', background: '#ffedb8' })
            return;
        }
        if (!itemsValidity.isValid) {
            console.log(itemsValidity.errors)
            setItemErrors([...itemsValidity.errors]);
            Jhilikka.fire({ icon: 'warning', title: 'Fix errros and re-submit!', background: '#ffedb8' })
            return;
        }

        setSubmitting(true);
        const {
            vendor_id, //will be a vendor object
            invoiceNo,
            invoicedOn,
            completedOn,
            orderStatus,
            extraDiscount,
            extraDiscountType,
        } = purchaseOrderData


        const orderJson = {
            'action': 'add',
            'third_party_invoice_number': invoiceNo,
            'invoiced_on': invoicedOn,
            'completed_on': completedOn,
            'discount_type': extraDiscountType,
            'discount': extraDiscount,
            'vendor': vendor_id,
            'bill_received': false,
            'status': orderStatus,
            'weight': 10, // will may removed
        }


        const itemsJson = { action: 'add_multiple', purchase_items: [] };
        for (let anItem of purchaseItemsData) {
            itemsJson.purchase_items.push({
                "item": anItem.item_id,
                "quantity": anItem.quantity,
                "defective": anItem.defective,
                "non_discount_price": anItem.price,
                "discount_type": anItem.discountType,
                "discount": anItem.discount,
                "status": anItem.itemStatus
            })
        }
        console.log(orderJson);
        console.log(itemsJson);
        createPurchaseOrder(orderJson)
            .then((res) => {
                console.log(res)
                if (res.json.status) {
                    console.log('order added successfully');
                    itemsJson.purchase_order = res.json.purchase_orders[0].id
                    createPurchaseItem({ ...itemsJson })
                        .then((res2) => {
                            console.log(res2)
                            if (res2.json.status) {
                                Jhilikka.fire({ icon: 'success', title: 'Purchase Order has been created!', background: '#dffff0' })
                                props.history.push('/purchase-order/' + res.json.purchase_orders[0].id)
                                console.log('Successfully purchase items added')
                            } else { Jhilikka.fire({ icon: 'error', title: res2.json.error, background: '#ffe5de' }) }
                        }).catch((err) => {
                            console.log(err)
                        });
                } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
            }).catch((err) => {
                console.log(err)
            })
            .then(() => {
                setSubmitting(false)
            });
    }

    // console.log(itemErrors)
    // console.log(purchaseOrderData);
    // console.log(purchaseItemsData);
    console.log(createItemModalData.activeRow)



    var orderSubTotal = 0
    const PurchaseItemsList = purchaseItemsData.map((item, index) => {
        var itemSubtotal = item.price * item.quantity;
        var itemTotal = item.discountType === 'fixed' ?
            itemSubtotal - (item.discount * item.quantity) :
            itemSubtotal - itemSubtotal * 0.01 * (item.discount || 0)
        orderSubTotal += itemTotal;
        return (
            <tr key={index} className='tableInputRow'>
                <th scope="row" >
                    <div
                        onClick={() => _removePurchaseItem(index)}
                        style={{ cursor: 'pointer', margin: '10px 0', border: '1px solid #f44236', borderRadius: '3px', width: '25px', textAlign: 'center' }}
                    >
                        <i className="feather icon-trash text-c-red f-15" />
                    </div>
                </th>

                <td className='table-input-long'>
                    <AsyncSelect
                        value={isEmpty(purchaseItemsData[index].item_id) ? null : { label: purchaseItemsData[index].itemName, value: purchaseItemsData[index].item_id }}
                        onChange={(value) => _selectedItemChange(index, value)}
                        loadOptions={loadItemOptions}
                        defaultOptions={defaultItemOptions}
                        placeholder='Start typing'
                        onInputChange={_itemInputChange}
                        components={{
                            MenuList: CustomSelectMenuList,
                            Menu: CustomSelectMenu,
                        }}

                        onMenuScrollToBottom={_scrollItemMenuToBottom}
                        onMenuClose={() => {
                            store.dispatch({ type: RESET_FETCH_MORE_OPTIONS })
                            store.dispatch({ type: SET_IS_MENU_OPEN, payload: ['item', false] })
                        }}
                        onMenuOpen={() => {
                            store.dispatch({ type: SET_IS_MENU_OPEN, payload: ['item', true] })
                            setCreateItemModalData({ ...createItemModalData, activeRow: index })
                        }}
                    />
                    <ErrorBody>{itemErrors[index] ? itemErrors[index].item_id : null}</ErrorBody>
                </td>

                <td className="table-input-short">
                    <Form.Control
                        onChange={(e) => _purchaseItemsDataChange(index, e)}
                        name='quantity'
                        value={purchaseItemsData[index].quantity}
                        type="number"
                        placeholder="Qty"
                    />
                    <ErrorBody>{itemErrors[index] ? itemErrors[index].quantity : null}</ErrorBody>
                </td>

                <td className="table-input-short">
                    <Form.Control
                        onChange={(e) => _purchaseItemsDataChange(index, e)}
                        name='defective'
                        value={purchaseItemsData[index].defective || 0}
                        type="number"
                        placeholder="Defective"
                    />
                    <ErrorBody>{itemErrors[index] ? itemErrors[index].defective : null}</ErrorBody>
                </td>

                <td className="table-input-medium">
                    <Form.Control
                        onChange={(e) => _purchaseItemsDataChange(index, e)}
                        name='price'
                        value={purchaseItemsData[index].price}
                        type="number"
                        placeholder="Price"
                    />
                    <ErrorBody>{itemErrors[index] ? itemErrors[index].price : null}</ErrorBody>
                </td>

                <td className="table-input-medium">
                    <Form.Control
                        readOnly
                        value={itemSubtotal}
                        type="number"
                        placeholder="Sub Total"
                    />
                </td>
                <td className="table-input-medium">
                    <Form.Control
                        style={{ display: 'inline', 'maxWidth': 'calc( 50% - 1px )', marginRight: '1px', paddingLeft: '2px', paddingRight: '1px' }}

                        onChange={(e) => _purchaseItemsDataChange(index, e)}
                        name='discount'
                        value={purchaseItemsData[index].discount}
                        type="number"
                        placeholder="Discount value "
                    />
                    <ErrorBody>{itemErrors[index] ? itemErrors[index].discount : null}</ErrorBody>

                    <Form.Control
                        style={{ display: 'inline', 'maxWidth': '50%', padding: '0 2px' }}
                        name='discountType'
                        value={purchaseItemsData[index].discountType}
                        onChange={(e) => _purchaseItemsDataChange(index, e)}
                        as="select"
                    >
                        <option value='' disabled >Type</option>
                        <option value='percent'>%</option>
                        <option value='fixed'>Fixed</option>
                    </Form.Control>
                    <ErrorBody>{itemErrors[index] ? itemErrors[index].discountType : null}</ErrorBody>

                </td>

                <td className="table-input-medium">
                    <Form.Control
                        readOnly
                        value={itemTotal}
                        type="number"
                        placeholder="Total"
                    />
                </td>

                <td className="table-input-medium">
                    <Form.Control
                        style={{ padding: '0 2px' }}
                        name='itemStatus'
                        value={purchaseItemsData[index].itemStatus}
                        onChange={(e) => _purchaseItemsDataChange(index, e)}
                        as="select"
                    >
                        <option value='' disabled >Status</option>
                        <option value='delivered'>Delivered</option>
                        <option value='incomplete'>Incomplete</option>
                        <option value='addedtocirculation'>In Circulation</option>
                    </Form.Control>
                    <ErrorBody>{itemErrors[index] ? itemErrors[index].itemStatus : null}</ErrorBody>
                </td>

            </tr>
        )
    });
    const extraDiscountAmount = purchaseOrderData.extraDiscountType === 'percent' ?
        orderSubTotal * 0.01 * purchaseOrderData.extraDiscount || 0 :
        purchaseOrderData.extraDiscount || 0;

    console.log(props.instanceCreationModal)

    if (loading) return <Loader />
    return (
        <Aux>
            <Row>
                <Col>
                    <Card>
                        <Card.Body>
                            <h5>Create Purchase Order</h5>
                            <hr />
                            <Row>
                                <Col sm={6} md={6} lg={6}>

                                    <Form.Group  >
                                        <Form.Label>Vendor Name*</Form.Label>
                                        <AsyncSelect
                                            defaultOptions={defaultVendorOptions}
                                            className="mousetrap"
                                            placeholder='Start typing'
                                            value={purchaseOrderData.vendor_id ? { label: purchaseOrderData.vendorName, value: purchaseOrderData.vendor_id } : null}
                                            isClearable={true}
                                            cacheOptions={true}
                                            loadOptions={loadVendorOptions}
                                            onChange={_selectedVendorChange}
                                            onInputChange={_vendorInputChange}
                                            components={{
                                                MenuList: CustomSelectMenuList,
                                                Menu: CustomSelectMenu,
                                            }}
                                            onMenuScrollToBottom={_scrollVendorMenuToBottom}
                                            onMenuClose={() => {
                                                store.dispatch({ type: RESET_FETCH_MORE_OPTIONS })
                                                store.dispatch({ type: SET_IS_MENU_OPEN, payload: ['vendor', false] })
                                            }}
                                            onMenuOpen={() => {
                                                store.dispatch({ type: SET_IS_MENU_OPEN, payload: ['vendor', true] })
                                            }}
                                        />
                                        <ErrorBody>{orderErrors.vendor_id}</ErrorBody>
                                    </Form.Group>
                                </Col>

                                <Col sm={6} md={6} lg={6}>
                                    <Form.Group  >
                                        <Form.Label>Third Party Invoice No.*</Form.Label>

                                        <Form.Control name="invoiceNo" value={purchaseOrderData.invoiceNo} onChange={_purchaseOrderDataChange} type="text" placeholder="Invoice No." className="mousetrap" />
                                        <ErrorBody>{orderErrors.invoiceNo}</ErrorBody>
                                    </Form.Group>
                                </Col>

                                <Col sm={3} md={3} lg={3}>

                                    <Form.Group  >
                                        <Form.Label>Invoiced On</Form.Label> <br />
                                        <DatePicker onChange={_invoicedOnChange} placeholder="Pick Date" name="invoicedOn" selected={purchaseOrderData.invoicedOn} className="mousetrap" />
                                        <ErrorBody>{orderErrors.invoicedOn}</ErrorBody>
                                    </Form.Group>
                                </Col>

                                <Col sm={3} md={3} lg={3}>
                                    <Form.Group  >
                                        <Form.Label>Completed On</Form.Label> <br />
                                        <DatePicker onChange={_completedOnChange} placeholder="Pick Date" name="completedOn" selected={purchaseOrderData.completedOn} className="mousetrap" />
                                        <ErrorBody>{orderErrors.completedOn}</ErrorBody>
                                    </Form.Group>
                                </Col>

                                <Col sm={6} md={6} lg={6}>
                                    <Form.Label>Status*</Form.Label>
                                    <Form.Control name='orderStatus' value={purchaseOrderData.orderStatus} onChange={_purchaseOrderDataChange} as="select" className="mousetrap">

                                        <option value='' disabled >Select Order Status</option>
                                        {orderStatuses.map((item, idex) => {
                                            return <option key={idex} value={item.id}>{item.name}</option>
                                        })}
                                    </Form.Control>
                                    <ErrorBody>{orderErrors.orderStatus}</ErrorBody>
                                </Col>

                                <Col md={2}></Col>
                                <Col md={8}>
                                    <Form.Group  >
                                        <Form.Control
                                            disabled={barcodeDisabled}
                                            title="Use ALT+A to quickly focus on this input!"
                                            name="enteredBarcode"
                                            type="text"
                                            value={purchaseOrderData.enteredBarcode}
                                            onKeyDown={_keydownBarcode}
                                            onChange={_purchaseOrderDataChange}
                                            size="lg"
                                            placeholder="Barcode Here (e.g. 9288)"
                                            ref={barcodeRef}
                                            className="mt-4 p-4 mousetrap"
                                        />
                                    </Form.Group>
                                </Col>
                                <Col md={2}></Col>


                            </Row>

                            <Card.Header>
                                <Card.Title as="h5">Purchase Items</Card.Title>
                            </Card.Header>
                            <div >
                                <Table responsive>
                                    <thead>
                                        <tr>
                                            <th></th>
                                            <th>Item Name*</th>
                                            <th>Quantity*</th>
                                            <th>Defective*</th>
                                            <th>Price*</th>
                                            <th>Sub-Total</th>
                                            <th>Discount</th>
                                            <th>Total</th>
                                            <th>Status*</th>
                                        </tr>
                                    </thead>
                                    <tbody >

                                        {PurchaseItemsList}

                                        <tr>
                                            <th scope="row">
                                                <div onClick={_addPurchaseItem} style={{ cursor: 'pointer', textAlign: 'center', border: '1px solid #1de9b6', borderRadius: '3px', width: '25px' }}>
                                                    <i className="feather icon-plus text-c-green f-15" />
                                                </div>
                                            </th>
                                            <td colSpan={8}></td>
                                        </tr>
                                        <tr>
                                            <th scope="row"></th>
                                            <th scope="row" colSpan={6}>Sub Total</th>
                                            <th colSpan={2}>Rs. <Num>{orderSubTotal}</Num></th>
                                        </tr>
                                        <tr>
                                            <th></th>
                                            <th scope="row" style={{ padding: '28px 0.75rem' }} colSpan={4}>Extra Discount</th>
                                            <td className="table-input-long" colSpan={2}>
                                                <Form.Control
                                                    style={{ display: 'inline', 'maxWidth': 'calc( 50% - 8px )', marginRight: '8px' }}
                                                    onChange={_purchaseOrderDataChange}
                                                    name='extraDiscount'
                                                    value={purchaseOrderData.extraDiscount}
                                                    type="number"
                                                    placeholder="Discount value "
                                                />
                                                <Form.Control
                                                    style={{ display: 'inline', 'maxWidth': '30%', padding: '0 2px' }}
                                                    name='extraDiscountType'
                                                    value={purchaseOrderData.extraDiscountType}
                                                    onChange={_purchaseOrderDataChange}
                                                    as="select"
                                                >
                                                    <option value='' disabled >Type</option>
                                                    <option value='percent'>%</option>
                                                    <option value='fixed'>Fixed</option>
                                                </Form.Control>
                                            </td>
                                            <th colSpan={2} style={{ padding: '28px 0.75rem' }}>Rs. <Num>{extraDiscountAmount}</Num></th>
                                        </tr>
                                        <tr>
                                            <th scope="row"></th>
                                            <th scope="row" colSpan={6} style={{ fontSize: '1.15em' }}>Total</th>
                                            <th colSpan={2} style={{ fontSize: '1.15em' }}>Rs. <Num>{orderSubTotal - extraDiscountAmount}</Num></th>
                                        </tr>
                                        <tr style={{ height: '70px' }}>
                                            <th scope="row">{' '}</th>
                                            <td colSpan={8}></td>
                                        </tr>
                                    </tbody>
                                    {/* <br/>
                                        <br/> */}
                                </Table>
                            </div>


                            <div className="d-flex justify-content-end" >
                                <Button
                                    variant="warning"
                                    onClick={() => { props.history.push('/purchase-orders') }}
                                >
                                    Cancel
                                </Button>
                                <Button

                                    disabled={submitting}
                                    title='Use ALT+S as shortcut'
                                    variant="primary"
                                    onClick={_submit}
                                >
                                    {submitting ? <><i className="fa fa-spinner text-c-white f-15 fa-pulse m-l-1"></i>Submitting</> : 'Submit'}

                                </Button>
                            </div>

                        </Card.Body>
                    </Card>
                    <CreateItemModal
                        show={props.instanceCreationModal.item.modalShowing}
                        onHide={() => store.dispatch({ type: SET_INSTANCE_CREATION_MODAL, payload: ['item', false] })}
                        setCreatedToSelected={(instance) => {
                            setDefaultItemOptions([{ ...instance }, ...defaultItemOptions])
                            _selectedItemChange(createItemModalData.activeRow, instance)
                        }}
                    />
                    <CreateVendorModal
                        show={props.instanceCreationModal.vendor.modalShowing}
                        onHide={() => store.dispatch({ type: SET_INSTANCE_CREATION_MODAL, payload: ['vendor', false] })}
                        setCreatedToSelected={(instance) => {

                            console.log(instance)

                            _selectedVendorChange(instance)
                            setDefaultVendorOptions([{ ...instance }, ...defaultVendorOptions])
                        }}
                    />
                </Col>
            </Row>
        </Aux >
    );
}



Create.propTypes = {
    instanceCreationModal: PropTypes.object.isRequired,
}

const mapStateToProps = state => ({
    instanceCreationModal: state.instanceCreationModal,
})

export default connect(mapStateToProps, {})(Create);